.creative {
  font-family: $creative;
  font-size: 3rem;
  line-height: 4.5rem;
  letter-spacing: .25rem;
  @media (min-width: 950px) {
    font-size: 5.5rem;
    letter-spacing: .5rem; }
  color: $white;
  -webkit-text-stroke-width: 1px; }

.coder {
  font-size: 3rem;
  @media (min-width: 950px) {
    font-size: 5rem; }
  font-family: $coder;
  color: $cool-blue;
  white-space: nowrap;
  overflow: hidden;
  border-right: .1em solid;
  width: 10ch;
  letter-spacing: -3px;
  animation: typing 2s steps(11), caret 1s steps(1) infinite; }
